.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
// .is-hidden {
//   display: none;
// }
.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 40px;
  margin: 46px 0 60px;
  line-height: normal;
  text-align: center;
  font-size: 0;
  border-radius: 5px;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  padding: 13px 0 11px;
  margin-left: 0;
  color: #000000;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  // border: 1px solid #ddd;
  // border-width: 1px 0;
}

.tui-pagination .tui-page-btn:hover {
  //background-color: #f4f4f4;
  color: #fff;
  background: #ff6b08;
  border-color: #ff6b08;
  border-radius: 5px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  border-color: #ff6b08;
  cursor: default;
  border-radius: 5px;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #ff6b08;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

